<template>
  <tr>
    <th scope="row">{{ index + 1 }}</th>
    <td>
      <input
        v-if="isEdit"
        type="date"
        v-model="form.infectedDate"
        class="form-control form-control-sm"
        :disabled="isUpdating"
      />
      <span v-else>{{
        patient.infectedDate.toDate().toLocaleDateString("bn-BD", {
          year: "numeric",
          month: "long",
          day: "numeric"
        })
      }}</span>
    </td>
    <td>
      <input
        type="text"
        v-if="isEdit"
        v-model="form.name"
        class="form-control form-control-sm"
        :disabled="isUpdating"
      />
      <span v-else>{{ patient.name }}</span>
    </td>
    <td>
      <select
        v-if="isEdit"
        v-model="form.occupationId"
        class="form-control form-control-sm"
        :disabled="isUpdating"
      >
        <option :value="null" disabled>Select Occupation</option>
        <option
          v-for="(occupation, index) in occupations"
          :value="occupation.id"
          :key="index"
          >{{ occupation.name }}</option
        >
      </select>
      <span v-else>{{
        getterSpecificOccupationById(patient.occupationId).name
      }}</span>
    </td>
    <td>
      <select
        v-if="isEdit"
        v-model="form.unionId"
        class="form-control form-control-sm"
        :disabled="isUpdating"
      >
        <option :value="null" disabled>Select Union</option>
        <option
          v-for="(union, index) in unions"
          :value="union.id"
          :key="index"
          >{{ union.name }}</option
        >
      </select>
      <span v-else>{{ getterSpecificUnionById(patient.unionId).name }}</span>
    </td>
    <td>
      <textarea
        v-if="isEdit"
        v-model="form.address"
        class="form-control form-control-sm"
        :disabled="isUpdating"
      ></textarea>
      <span v-else>{{ patient.address }}</span>
    </td>
    <td>
      <button
        class="btn btn-sm btn-info mr-2"
        v-if="!isEdit"
        @click="isEdit = true"
        :disabled="isDeleting"
      >
        Edit
      </button>
      <button
        class="btn btn-sm btn-danger"
        v-if="!isEdit"
        @click="deletePatient"
        :disabled="isDeleting"
      >
        <div
          v-if="isDeleting"
          class="spinner-border spinner-border-sm mb-2"
          role="status"
        >
          <span class="sr-only">Deleting</span>
        </div>
        Delete
      </button>
      <button
        class="btn btn-sm btn-success mr-2"
        v-if="isEdit"
        @click="updatePatient"
        :disabled="isUpdating"
      >
        <div
          v-if="isUpdating"
          class="spinner-border spinner-border-sm mb-2"
          role="status"
        >
          <span class="sr-only">Updating</span>
        </div>
        Update
      </button>
      <button class="btn btn-sm btn-info" v-if="isEdit" @click="isEdit = false" :disabled="isUpdating">
        Cancel
      </button>
    </td>
  </tr>
</template>

<script>
import { firestore, Timestamp } from "../../../config/firebase";
import { mapGetters } from "vuex";
export default {
  name: "PatientItemComponent",
  props: {
    patient: {
      type: Object,
      required: true
    },
    index: {
      type: Number,
      required: true
    },
    unions: {},
    occupations: {}
  },
  watch: {
    patient: {
      handler: function(val) {
        this.form.infectedDate = val.infectedDate
          .toDate()
          .toISOString()
          .substr(0, 10);
        this.form.name = val.name;
        this.form.occupationId = val.occupationId;
        this.form.unionId = val.unionId;
        this.form.address = val.address;
      },
      immediate: true,
      deep: true
    }
  },
  computed: {
    ...mapGetters(["getterSpecificOccupationById", "getterSpecificUnionById"])
  },
  data: () => ({
    isEdit: false,
    form: {
      infectedDate: "",
      name: "",
      occupationId: "",
      unionId: "",
      address: ""
    },
    isUpdating: false,
    isDeleting: false
  }),
  methods: {
    async updatePatient() {
      if (!this.form.name) {
        alert("Name can not be empty");
        return;
      }
      if (!this.form.occupationId) {
        alert("Occupation can not be empty");
        return;
      }
      if (!this.form.unionId) {
        alert("Union can not be empty");
        return;
      }
      if (!this.form.address) {
        alert("Address can not be empty");
        return;
      }
      if (!this.form.infectedDate) {
        alert("Infected date can not be empty");
        return;
      }

      // show preloader
      this.isUpdating = true;
      try {
        const docRef = firestore.collection("patients").doc(this.patient.id);

        await docRef.update({
          name: this.form.name,
          occupationId: this.form.occupationId,
          unionId: this.form.unionId,
          address: this.form.address,
          infectedDate: Timestamp.fromDate(new Date(this.form.infectedDate)),
          updatedAt: Timestamp.now()
        });

        // change edit status
        this.isEdit = false;
      } catch (e) {
        console.log(e);
      }

      // hide preloader
      this.isUpdating = false;
    },
    async deletePatient() {
      if (!confirm("Are you sure want to delete?")) {
        return;
      }
      // show preloader
      this.isDeleting = true;
      try {
        const docRef = firestore.collection("patients").doc(this.patient.id);
        await docRef.delete();
      } catch (e) {
        alert("Failed to delete. Try again later");
      }

      // hide preloader
      this.isDeleting = false;
    }
  }
};
</script>

<style scoped></style>
