<template>
  <table class="table table-hover">
    <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">শনাক্তের তারিখ</th>
        <th scope="col">নাম</th>
        <th scope="col">পেশা</th>
        <th scope="col">ইউনিয়ন</th>
        <th scope="col">ঠিকানা</th>
        <th scope="col">Action</th>
      </tr>
    </thead>
    <tbody>
      <tr v-if="patients === null">
        <td colspan="7">
          <preloader-component />
        </td>
      </tr>
      <tr v-else-if="patients.length === 0">
        <td colspan="7" class="text-center">
          No patient is available
        </td>
      </tr>
      <!--Patient item component-->
      <patient-item-component
        v-else
        v-for="(patient, index) in patients"
        :key="index"
        :index="index"
        :patient="patient"
        :unions="unions"
        :occupations="occupations"
      />
    </tbody>
  </table>
</template>

<script>
import { mapState, mapActions } from "vuex";
import PreloaderComponent from "../../common/PreloaderComponent";
import PatientItemComponent from "./PatientItemComponent";
export default {
  name: "PatientListComponent",
  components: { PatientItemComponent, PreloaderComponent },
  computed: {
    ...mapState(["unions", "occupations", "patients"])
  },
  methods: {
    ...mapActions(["bindOccupations", "bindUnions", "bindPatients"]),
    initData() {
      if (this.unions === null) {
        this.bindUnions();
      }

      if (this.occupations === null) {
        this.bindOccupations();
      }

      if (this.patients === null) {
        this.bindPatients();
      }
    }
  },
  mounted() {
    this.initData();
  }
};
</script>

<style scoped></style>
