<template>
  <form @submit.prevent="submitForm" ref="patientForm">
    <div class="form-group row">
      <label for="infected-date" class="col-sm-3 col-form-label"
        >শনাক্তের তারিখ</label
      >
      <div class="col-sm-9">
        <input
          type="date"
          v-model="form.infectedDate"
          class="form-control"
          id="infected-date"
          required
          :disabled="isSaving"
        />
      </div>
    </div>

    <div class="form-group row">
      <label for="name" class="col-sm-3 col-form-label">নাম</label>
      <div class="col-sm-9">
        <input
          type="text"
          v-model="form.name"
          class="form-control"
          id="name"
          required
          :disabled="isSaving"
          placeholder="রোগীর নাম লিখুন"
        />
      </div>
    </div>

    <div class="form-group row">
      <label for="occupation-id" class="col-sm-3 col-form-label">পেশা</label>
      <div class="col-sm-9">
        <select
          v-model="form.occupationId"
          class="form-control"
          id="occupation-id"
          v-if="occupations != null"
          required
          :disabled="isSaving"
        >
          <option :value="null" disabled>পেশা নির্বাচন করুন</option>
          <option
            v-for="(occupation, index) in occupations"
            :value="occupation.id"
            :key="index"
            >{{ occupation.name }}</option
          >
        </select>
      </div>
    </div>

    <div class="form-group row">
      <label for="union-id" class="col-sm-3 col-form-label">ইউনিয়ন</label>
      <div class="col-sm-9">
        <select
          v-model="form.unionId"
          class="form-control"
          id="union-id"
          required
          :disabled="isSaving"
        >
          <option :value="null" disabled>ইউনিয়ন নির্বাচন করুন</option>
          <option
            v-for="(union, index) in unions"
            :value="union.id"
            :key="index"
            >{{ union.name }}</option
          >
        </select>
      </div>
    </div>

    <div class="form-group row">
      <label for="address" class="col-sm-3 col-form-label">ঠিকানা</label>
      <div class="col-sm-9">
        <textarea
          v-model="form.address"
          class="form-control"
          id="address"
          required
          :disabled="isSaving"
          placeholder="রোগীর ঠিকানা লিখুন"
        ></textarea>
      </div>
    </div>

    <div class="form-group row">
      <div class="col-sm-9 offset-sm-3">
        <button
          type="submit"
          class="btn btn-success btn-block"
          :disabled="isSaving"
        >
          <div
            v-if="isSaving"
            class="spinner-border spinner-border-sm mb-2"
            role="status"
          >
            <span class="sr-only">Saving</span>
          </div>
          Save
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import { firestore, Timestamp } from "../../../config/firebase";
import { mapState, mapActions } from "vuex";
export default {
  name: "PatientFormComponent",
  computed: {
    ...mapState(["unions", "occupations"])
  },
  data: () => ({
    form: {
      infectedDate: null,
      name: null,
      occupationId: null,
      unionId: null,
      address: null
    },
    isSaving: false
  }),
  methods: {
    ...mapActions(["bindUnions", "bindOccupations"]),
    initData() {
      // if unions is null then bind unions
      if (this.unions === null) {
        this.bindUnions();
      }
      // if occupation is null then bind occupation
      if (this.occupations === null) {
        this.bindOccupations();
      }
    },
    resetForm() {
      this.form = {
        infectedDate: null,
        name: null,
        occupationId: null,
        unionId: null,
        address: null
      };
    },
    async submitForm() {
      // show preloader
      this.isSaving = true;

      const data = { ...this.form };

      try {
        const docRef = firestore.collection("patients").doc();

        // set id
        data["id"] = docRef.id;

        // set infected date to timestamp
        data["infectedDate"] = Timestamp.fromDate(
          new Date(this.form.infectedDate)
        );

        data["createdAt"] = Timestamp.now();
        data["updatedAt"] = Timestamp.now();

        await docRef.set(data);

        //reset form
        this.resetForm();
      } catch (e) {
        alert("Failed to save patient. Try again later.")
      }

      // hide preloader
      this.isSaving = false;
    }
  },
  mounted() {
    this.initData();
  }
};
</script>

<style scoped></style>
